import csstype.px
import csstype.rgb
import emotion.react.css
import kotlinx.browser.document
import org.w3c.dom.*
import react.create
import react.dom.client.createRoot

fun main() {
    /*
    val container = document.createElement("div")
    document.body!!.appendChild(container)
    println(document.URL)
    val welcome = Welcome.create {
        name = document.body!!.innerHTML.toString()
    }
    createRoot(container).render(welcome)*/

    val name: String = document.getElementsByTagName("h1").get(0)!!.innerHTML.toString()

    val ws = WebSocket("ws://localhost:8080/main/chats/${name}").apply {
        onopen = {
            println("connected")
            println(document.URL)
        }
        onmessage = {
            println("We got a message: ${it.data as String}")
            val div = document.getElementById("history") as HTMLDivElement

            div.innerHTML = "${div.innerHTML}<br>${it.data as String}"

            Unit
        }
    }

    (document.getElementById("send") as HTMLInputElement).apply {
        onclick = {
            val message = (document.getElementById("message") as HTMLInputElement).value

            ws.send(message)
        }
    }

    (document.getElementById("clear") as HTMLButtonElement).apply {
       value = "Clear"
        onclick = {
            ws.send("cleaning")
        }
    }
}